import { NgModule, NO_ERRORS_SCHEMA } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SharedModule } from '../../../shared/modules/shared.module';
import { DirectPoMasterRoutingModule } from './direct-po-master-routing.module';
import { DirectPoMasterComponent } from './direct-po-master.component';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    DirectPoMasterRoutingModule,
  ],
  declarations: [
    DirectPoMasterComponent,
  ],
  schemas:[NO_ERRORS_SCHEMA]
})
export class DirectPoMasterModule { }



